h3 {
  font-size: 40px;
}

a {
  text-decoration: none;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
    "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}

.App-logo.fallback-wrap {
  pointer-events: none;
  height: 50vh;
}

.Fallback-section {
  height: 100vh;
  width: 100vw;
  display: flex;
  scroll-behavior: smooth;
  justify-content: center;
  align-items: center;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo.fallback-wrap {
    animation: App-logo-spin infinite 2s linear;
  }
  /* .App-logo.header-wrap {
    animation: Header-logo-spin infinite 10s alternate-reverse;
  } */
}

.App {
  display: flex;
  flex-direction: column;
  position: relative;
  /* min-height: 100vh;
  min-width: 100vw; */
  /* border: 1px solid white; */
}

.App-header {
  padding-inline: 30px;
  display: flex;
  max-width: 100vw;
  height: 100px;
  justify-content: space-between;
  /* border: 1px solid white; */
}

.App-logo.header-wrap {
  height: 80px;
  pointer-events: none;
}

.header-image {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
}

.header-image:hover {
  transform: scale(1.5);
  animation: Header-logo-spin infinite 5s linear;
}

.header-content {
  display: flex;
  /* color: #61dafb; */
  /* border: 1px solid white; */
}

.header-links {
  display: flex;
  margin-left: auto;
  font-size: 22px;
  list-style: none;
  align-items: center;
}

.header-links li {
  margin-right: 10px;
}

.Resume-box {
  border-radius: 10px;
  padding: 10px;
  font-size: 18px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes Header-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.main-wrap {
  width: 100%;
  max-height: 100vh;
  padding-inline: 100px;
  position: absolute;
  /* border: 1px solid whitesmoke; */
}

.mf-wrapper {
  /* border: 1px solid whitesmoke; */
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  min-height: 100vh;
}

.mf-para.title {
  font-size: 20px;
  font-family: "Courier New", Courier, monospace;
  /* border: 2px solid white; */
}

.mf-para.desc {
  margin: 40px 0 0 0;
  width: 40vw;
  text-align: left;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  font-size: 18px;
  line-height: 1.5;
}

.mf-headers#name {
  font-size: 100px;
}

.mf-headers#role {
  font-size: 40px;
  font-family: sans-serif;
}

.mf-ct {
  /* border: 1px solid white; */
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 80vh;
}

.ct-content {
  margin: 30px;
}

.mf-ct a {
  padding: 23px 30px;
  border-radius: 8px;
}

.mf-ct.ic {
  /* border: 1px solid white; */
  margin: 40px 0px;
  display: flex;
  align-items: inherit;
  height: fit-content;
  width: fit-content;
}

.footer-fin {
  display: flex;
  padding: 2%;
  justify-content: center;
  color: snow;
  font-size: 14px;
}

.social-container-wrap {
  position: fixed;
  width: 100px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-end;
}

.s-logo {
  height: 25px;
  margin: 0px 0px 25px 0px;
}

.vl {
  border: 1px solid grey;
  height: 200px;
}